.Invoice-Style {
  position: fixed;
  text-align: center;
  top: 5em;
  right: 0;
  width: 45%;
  height: 100vh;
  background-color: #f2f6f3;
}

.Invoice-Style header h4 {
  font-weight: 400;
  color: #b1b5b2;
  margin: 1em 0 0.5em;
}

.Invoice-Style .format {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: 0 auto 1em;
}

.Invoice-Style .format figure {
  /* width: 80%; */
  height: 260px;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  margin: 0 1em 1em;
  overflow: hidden;
}

.Invoice-Style .format figure img {
  /* width: 100%; */
  height: 100%;
}

.Invoice-Style .format label {
  width: 10%;
  margin: auto 0;
}

.Invoice-Style .format label input {
  width: 80px;
  margin-left: -60px;
  opacity: 0;
}

.Invoice-Style .color {
  width: 40%;
  margin: 0 auto;
}

.Invoice-Style .color img {
  position: absolute;
  width: 18px;
  margin: 1.1em 1em;
}

.Invoice-Style .color h4 {
  font-size: 0.875rem;
  font-weight: 500;
  color: #b1b5b2;
}

.Invoice-Style .color ul {
  display: flex;
  justify-content: space-between;
  margin-top: 1em;
}

.Invoice-Style .color li {
  display: flex;
}

.Invoice-Style .color li span {
  display: inline-block;
  width: 50px;
  height: 50px;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 50%;
}

.Invoice-Style .color li input {
  width: 50px;
  height: 50px;
  margin-left: -50px;
  opacity: 0;
}

.Invoice-Style footer {
  width: 40%;
  text-align: left;
  margin: 1em auto;
}

.Invoice-Style footer h4 {
  font-size: 0.875rem;
  color: #b1b5b2;
  font-weight: 500;
  margin-bottom: 0.5em;
}

.Invoice-Style footer select {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  padding: 0.8em;
  border: none;
  border-radius: 5px;
}

@media (max-width: 30em) {
  .Invoice-Style .format {
    width: 80%;
    height: auto;
  }

  .Invoice-Style .format figure {
    height: auto;
  }

  .Invoice-Style .format figure img {
    height: auto;
  }

  .Invoice-Style .color {
    width: 70%;
  }

  .Invoice-Style footer {
    width: 70%;
  }
}
