.Untamed-Night {
  position: relative;
  width: 210mm;
  height: 297mm;
  padding: 4em;
  background-color: #fff;
  letter-spacing: 0.01rem;
}

.Untamed-Night .invoice-number {
  font-size: 0.875rem;
  color: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: space-between;
  margin-bottom: 3em;
}

.Untamed-Night h1 {
  font-size: 3rem;
  margin-bottom: 1em;
}

.Untamed-Night .information .Card {
  display: flex;
  margin-bottom: 2em;
  line-height: 1.1rem;
}

.Untamed-Night .information .Card:nth-child(3) {
  margin-bottom: 0.6em;
}

.Untamed-Night .information .Card h3 {
  font-size: 0.87rem;
  width: 100px;
  margin-right: 1em;
}

.Untamed-Night .information .Card ul,
.Untamed-Night .information .Card h4 {
  font-weight: normal;
  font-size: 0.875rem;
  color: rgba(0, 0, 0, 0.4);
}

.Untamed-Night .information ul li span {
  display: block;
}

.Untamed-Night .details {
  margin-top: 5em;
}

.Untamed-Night .details ul {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.5em;
}

.Untamed-Night .details > ul:first-of-type {
  font-weight: 600;
  color: rgba(0, 0, 0, 1);
}

.Untamed-Night .details .items {
  color: rgba(0, 0, 0, 0.4);
  font-size: 0.875rem;
  line-height: 1.1rem;
}

.Untamed-Night .details li:nth-child(1) {
  width: 40%;
}

.Untamed-Night .details li:nth-child(2),
.Untamed-Night .details li:nth-child(4) {
  width: 23%;
}

.Untamed-Night .details li:nth-child(3) {
  width: 10%;
}

.Untamed-Night .details li:nth-child(4) {
  text-align: right;
}

.Untamed-Night .total {
  float: right;
  text-align: right;
  margin-top: 3em;
}

.Untamed-Night .total ul {
  display: flex;
  flex-direction: column;
}

.Untamed-Night .details .total ul li {
  font-weight: 600;
  width: 100%;
}

.Untamed-Night .details .total ul li:first-of-type {
  color: rgba(0, 0, 0, 0.4);
  font-size: 0.75rem;
  text-transform: uppercase;
  margin-bottom: 0.5em;
}

.Untamed-Night .details .total ul li:last-of-type span:last-of-type {
  color: rgba(0, 0, 0, 0.4);
  font-size: 0.75rem;
  margin-left: 0.8em;
}

.Untamed-Night .details .total ul:last-of-type li:last-of-type {
  font-size: 2rem;
}

.Untamed-Night footer {
  position: absolute;
  bottom: 4em;
  right: 4em;
  text-align: right;
}

.Untamed-Night footer p {
  color: rgba(0, 0, 0, 0.4);
  font-size: 0.75rem;
}

.Untamed-Night footer ul {
  text-transform: uppercase;
  font-size: 0.875rem;
  display: flex;
  justify-content: flex-end;
  margin-top: 0.5em;
}

.Untamed-Night footer ul li {
  display: flex;
  color: rgba(0, 0, 0, 0.4);
  margin-left: 1em;
}

.Untamed-Night footer ul li span:last-of-type {
  color: rgba(0, 0, 0, 1);
  font-weight: 600;
  margin-left: 0.3em;
}

.Untamed-Night footer ul li:first-of-type span:first-of-type {
  display: none;
}

.Untamed-Night footer ul li span:first-of-type {
  display: inline-block;
  width: 5px;
  height: 5px;
  background-color: rgba(0, 0, 0, 1);
  border-radius: 50%;
  margin: auto 0;
  margin-right: 1em;
}
