.Print-Invoice {
  position: relative;
  width: 210mm;
  height: 297mm;
  margin: 0 auto;
  padding-top: 8em;
  background-color: #fff;
}

.Print-Invoice input {
  color: #f16162;
  border: none;
  background-color: transparent;
}

.Print-Invoice header {
  position: relative;
  width: 80%;
  margin: 0 auto 4em;
}

.Print-Invoice header h1 {
  display: flex;
  /* justify-content: space-between;s */
}

.Print-Invoice header h1 > input:last-of-type {
  color: #7fbcbe;
  height: 20px;
  border: 1px solid #7fbcbe;
}

.Print-Invoice header h1 input:first-of-type {
  position: relative;
  z-index: 1;
}

.Print-Invoice header h1 span {
  display: inline-block;
  margin: auto 0;
}

.Print-Invoice header h1 img {
  height: 28px;
}

.Print-Invoice header div {
  font-size: 0.75rem;
  font-weight: 500;
  color: #737574;
  display: flex;
  justify-content: space-between;
  margin-top: 3em;
}

.Print-Invoice header ul li {
  margin-bottom: 0.5em;
}

.Print-Invoice header ul li input {
  color: #f16162;
  border: none;
  margin-bottom: 0.3em;
}

.Print-Invoice header ul li span:last-of-type {
  font-weight: 600;
}

.Print-Invoice header ul li:first-of-type {
  color: #323742;
  border: none;
}

.Print-Invoice header ul li:first-of-type span:first-of-type {
  margin-right: 0.5em;
}

.Print-Invoice header ul li:first-of-type span:last-of-type {
  font-size: 1.25rem;
}

.Print-Invoice header ul:last-of-type {
  text-align: right;
}

.Print-Invoice .details {
  width: 80%;
  margin: 0 auto;
}

.Print-Invoice .details ul {
  display: flex;
  justify-content: space-between;
  color: #323742;
  font-size: 0.875rem;
  font-weight: 600;
  /* margin-top: 2em; */
}

.Print-Invoice .details ul li {
  width: 25%;
  margin-bottom: 1em;
}

.Print-Invoice .details ul li:last-of-type,
.Print-Invoice .details ul li:last-of-type input {
  text-align: right;
}

.Print-Invoice .details ul li:first-of-type {
  width: 33%;
}

.Print-Invoice .details ul li:last-of-type {
  width: 25%;
  color: rgba(127, 188, 189, 1);
}

.Print-Invoice .details ul li:nth-child(3) {
  width: 15%;
}

.Print-Invoice .details > ul:first-of-type {
  font-size: 0.75rem;
  font-weight: 600;
  color: #737574;
  text-transform: uppercase;
  margin-bottom: 2em;
  padding-bottom: 1em;
  border-bottom: 1px solid #737574;
}

.Print-Invoice .details > ul:first-of-type li {
  color: #737574;
  font-weight: 600;
  margin: 0;
}

.Print-Invoice footer {
  position: absolute;
  bottom: 0;
  padding: 4em 0;
  width: 100%;
  height: 280px;
  background-color: rgba(0, 0, 0, 0.05);
}

.Print-Invoice footer div {
  width: 80%;
  margin: 0 auto;
}

.Print-Invoice footer .total ul {
  color: #737574;
  font-size: 0.75rem;
  font-weight: 600;
  display: flex;
  justify-content: space-between;
  padding-bottom: 1em;
  border-bottom: 1px solid #000;
}

.Print-Invoice footer .total ul:first-child {
  text-transform: uppercase;
}

.Print-Invoice footer .total ul:last-of-type {
  padding: 2em 0;
}

.Print-Invoice footer .total ul li {
  width: 30%;
}

.Print-Invoice footer .total ul li:nth-child(1) {
  width: 40%;
}

.Print-Invoice footer .total ul:last-of-type span {
  display: block;
  margin-bottom: 0.4em;
}

.Print-Invoice footer .total ul:last-of-type span input {
  width: 65%;
  margin: auto 0;
}

.Print-Invoice footer .total ul:last-of-type li:nth-child(2) {
  color: #323742;
  font-size: 1.5rem;
}

.Print-Invoice footer .total ul:last-of-type li:nth-child(3) {
  color: rgba(127, 188, 189, 1);
  font-size: 1.5rem;
}

.Print-Invoice footer .contacts {
  display: flex;
  justify-content: space-between;
  margin-top: 2em;
}

.Print-Invoice footer .contacts h2 {
  width: 30%;
}

.Print-Invoice footer .contacts ul {
  display: flex;
  font-size: 0.675rem;
  margin: auto 0;
}

.Print-Invoice footer .contacts ul li,
.Print-Invoice footer .contacts input {
  /* width: 90%; */
  display: flex;
  text-align: right;
}

.Print-Invoice footer .contacts input {
  width: 80%;
}

.Print-Invoice footer .contacts ul img {
  height: 6px;
  margin: auto 0.4em;
}
