.App {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
}

.App .create {
  display: flex;
  margin: 22.5% auto 0;
  border: none;
  background-color: transparent;
}

.App .create span {
  font-size: 1rem;
  font-weight: 600;
  display: inline-block;
  height: 55px;
  padding: 01em;
  color: #7fbcbe;
  width: 220px;
  margin-right: 1em;
  border: 2px solid #7fbcbe;
  border-radius: 35px;
}

.App .create img {
  width: 55px;
  border-radius: 50%;
}

.App .btn {
  display: flex;
  width: 210mm;
  margin: 0 auto;
}

.App .btn button {
  color: #fff;
  font-size: 1rem;
  width: 100%;
  height: 50px;
  background-color: #7fbcbe;
  border: none;
  margin-bottom: 1em;
}

.App .btn button:first-of-type {
  border-right: 1px solid rgba(255, 255, 255, 0.25);
}

@media (max-width: 50em) {
  .App .create {
    margin: 65% auto 0;
  }
}

@media (max-width: 30em) {
  /* .App {
    height: 100vh;
    overflow: scroll;
  } */

  .App .create {
    margin: auto;
  }

  .App .create span {
    font-size: 0.875rem;
    width: 180px;
    height: 42px;
    padding: 0.8em;
  }

  .App .create img {
    width: 42px;
  }

  .App .btn {
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    flex-direction: row;
    z-index: 1;
  }

  .App .btn button {
    width: 100%;
    border-right: 1px solid rgba(255, 255, 255, 0.1);
  }
}
