.Aqua-Theme {
  position: relative;
  width: 210mm;
  height: 297mm;
  background-color: #fff;
}

.Aqua-Theme header {
  display: flex;
  flex-wrap: wrap;
  padding: 5em 3em 4em;
}

.Aqua-Theme header h4 {
  color: #7fbcbe;
  text-transform: uppercase;
  width: 100%;
}

.Aqua-Theme header h1 {
  font-size: 4rem;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  font-weight: 600;
}

.Aqua-Theme header h5 {
  color: rgba(0, 0, 0, 0.25);
  margin-top: 3.2em;
  margin-left: 0.8em;
}

.Aqua-Theme header h5 span:first-of-type {
  color: #000;
  font-weight: 900;
  font-style: italic;
  margin-right: 0.5em;
}

.Aqua-Theme header p {
  width: 100%;
}

.Aqua-Theme header p span {
  font-style: italic;
  font-weight: 900;
  margin-right: 0.5em;
}

.Aqua-Theme .details {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  position: relative;
  height: 70vh;
  /* background-color: rgba(127, 188, 190, 0.7); */
  background-color: aliceblue;
  padding: 2em 3em;
}

.Aqua-Theme .details ul {
  font-size: 0.75rem;
  display: flex;
  justify-content: space-between;
}

.Aqua-Theme .details > ul:first-of-type {
  text-transform: uppercase;
  font-weight: 600;
  padding-bottom: 0.8em;
  margin-right: 25%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

.Aqua-Theme .details .items {
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 1em;
  padding: 1em 0;
  margin-right: 25%;
  /* width: 80%; */
  height: 70%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

.Aqua-Theme .details .items li {
  margin-bottom: 1em;
}

.Aqua-Theme .details ul li:nth-child(1) {
  width: 53%;
}

.Aqua-Theme .details ul li:nth-child(2) {
  text-align: left;
  width: 25%;
}

.Aqua-Theme .details ul li:nth-child(3) {
  width: 18%;
}

.Aqua-Theme .details .total {
  text-align: right;
  position: absolute;
  width: 40%;
  height: 20%;
  bottom: 1em;
  right: 30%;
}

.Aqua-Theme .details .total li {
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 1em;
}

.Aqua-Theme .details .total ul li:last-of-type {
  text-align: right;
  color: rgba(0, 0, 0, 1);
  font-weight: 600;
}

.Aqua-Theme .details .total ul li:last-of-type span:last-of-type {
  margin-left: 0.5em;
}

.Aqua-Theme footer {
  width: 70%;
  margin: 5em 3em;
}

.Aqua-Theme footer .appriciation {
  display: flex;
  margin-bottom: 3em;
  padding: 0.5em 0;
  border-top: 2px solid rgba(0, 0, 0, 0.1);
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
}

.Aqua-Theme footer .appriciation h2 {
  font-size: 3rem;
}

.Aqua-Theme footer .appriciation p {
  margin-top: 1.7em;
}

.Aqua-Theme footer .appriciation p span {
  font-style: italic;
  display: inline;
  font-weight: 600;
  margin-right: 0.5em;
}

.Aqua-Theme .contact {
  color: rgba(0, 0, 0, 0.5);
  font-size: 0.875rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  line-height: 1rem;
}

.Aqua-Theme .contact ul {
  width: 48%;
}

.Aqua-Theme .contact ul li {
  display: flex;
  margin-bottom: 1em;
}

.Aqua-Theme .contact ul li:first-of-type {
  color: rgba(0, 0, 0, 1);
  font-weight: 600;
  text-transform: uppercase;
  font-size: 1rem;
}

.Aqua-Theme .contact ul:first-of-type li {
  flex-wrap: wrap;
}

.Aqua-Theme .contact ul:first-of-type li span {
  display: flex;
  width: 100%;
}

.Aqua-Theme .contact ul:last-of-type li span:first-of-type {
  width: 100px;
}

.Aqua-Theme .contact ul:last-of-type li span:last-of-type {
  font-weight: 900;
  margin-left: 0.5em;
}
